
























































import MainView from '@/components/MainView.vue'
import { Vue, Component } from 'vue-property-decorator'
@Component({
	components: {
		MainView,
	},
})
export default class ProductUpload extends Vue {
	data: FormData | null = null
	sqliteInfo = {
		sitename: 'fushi',
		pcode: 2,
		acode: 'cn',
		isFileName: true,
	}

	files = {
		md: 'mdi-language-markdown',
		png: 'mdi-file-image',
		txt: 'mdi-file-document-outline',
	}
	items = [
		{
			name: 'product',
			children: [
				{
					name: '冰箱',
					children: [
						{
							name: 'logo.png',
							file: 'png',
						},
					],
				},
				{
					name: '洗衣机',
					children: [
						{
							name: 'logo.png',
							file: 'png',
						},
					],
				},
				{
					name: '空调',
					children: [
						{
							name: 'logo.png',
							file: 'png',
						},
					],
				},
			],
		},
		{
			name: '878490a6ab380cf5bb4e7c97387e6850.db',
			file: 'txt',
		},
	]

	hadUploaded = false
	isUploading = false
	downloadUrl = ''
	fileChange(file: File) {
		this.data = new FormData()
		this.hadUploaded = false
		if (file) {
			this.data?.append('file', file)
		}
	}

	async upload() {
		this.isUploading = true

		let { data } = await this.$http.post('upload/product', this.data, {
			headers: {
				'Content-Type': 'multipart/form-data',
			},
		})

		this.downloadUrl = (
			await this.$http.post('product', {
				sqliteInfo: this.sqliteInfo,
				file: data[0],
			})
		).data
		this.$nextTick().then(_ => {
			this.hadUploaded = true

			this.isUploading = false
		})
	}
}
